@gray: #E9E7E2;
@deepGray: #888888;
@black: #060606;
@border: #8D9192;
@write: #ffffff;
@black1: #3F3F3F;
@width: 200px;
@width150: 160px;
@blue: #3B9DFF;
@black2: #000000;
@bule1: #3F3F3F;
@blue2: #1D1D1D;
@gray1: #565C6E;
@lineBorder: 1px;
@lineBorderSelec: 2px;
@selecBorderColor: red;
@selecBoxBorderColor: #3B9DFF;
@imgBoxDistance: 1px; //图像之间的间隔
@imgBoxColor: #ffffff;
html, body {
  -webkit-text-size-adjust: none !important;
}

.font-write {
  color: @write;
}

.activeUl {
  border: 0.1em solid #FF7D6F;
}

//table li
.activeLi {
  color: @write;
  background: @bule1;
}

.setBoxLi {
  background: @deepGray;
  color: @write;
}

.directionStyle {
  background: @gray1 !important;
}

.font12 {
  font-size: 12px;
}
.font10 {
  font-size: 10px;
}

.opacity (@opacity)//????? eg:0.x
{
  opacity: @opacity;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(opacity=(@opacity * 100))";
  filter: "alpha(opacity = (@opacity * 100))";
}

.activeStyle-background {
  background: @deepGray !important;
}

.activeStyle-boder {
  border: 1px solid @deepGray !important;
}

//标题公共样式
.publicTitle {
  padding: 2px 0 2px 10px;
  width: 100%;
  height: 30px;
  color: @write;
  .left {
    float: left;
    .info {
      display: block;
      height: 12px;
      font-size: 12px;

    }
  }
  .right {
    line-height: 26px;
    float: right;
    width: 30px;
    text-align: center;
    .icon {
      width: 100%;
      line-height: 26px;
    }
  }

}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

input, select {
  color: #000000 !important;
}

html {

  width: 100%;
  height: 100%;
  body {
    font-size: 14px;
    color: @write;
    width: 100%;
    height: 100%;
    overflow: hidden;
    .main {
      display: none;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      z-index: 10000;

      .txt {
        width: 100%;
        height: 30px;
        background: @deepGray;
        color: @write;
        line-height: 30px;
        text-align: center;
        position: relative;

        .closeBtn {
          position: absolute;
          right: 10px;
          top: 0;
        }
      }
      .context {
        .leftBox {
          background: @black;
          width: @width150;
          height: 100%;
          padding: 0 0 5px 0;
          overflow-y: scroll;
          float: left;
          .title {
            .publicTitle;
            background: @black1;

          }
          .imgBox {
            .orderUl {
              border: 1px solid @blue2;
              background: @blue2;
              //height: 174px;
              height: 190px;
              overflow: hidden;
              cursor: pointer;
              .orderLi {
                .orderTitle {
                  .publicTitle;
                  //line-height: 30px;
                  line-height: 15px;
                }
              }

            }
            .imgUl {
              padding: 2px 5px;
              background: @black;
              .imgLi {
                width: 130px;
                height: 130px;
                border: 1px solid @border;
                margin-top: 5px;
              }
              .imgLiSelected {
                border: @lineBorderSelec solid @selecBorderColor !important;;
              }
            }
          }
        }
        .contentBox {
          width: 100%;
          padding-left: @width150;
          padding-right: @width;
          background: #E9E7E2;
          height: 100%;
          .printPageBox {
            width: 100%;
            height: 100%;
            //padding: 30px;
            padding: 2px;
            //min-width: 1000px;
            //overflow-x: scroll;
            position: relative;

            //按钮区域，添加图像操作状态，如定位线显示====
            .printBtnaddArea {
              width: 40px;
              height: 40px;
              background-color: transparent;
              position: absolute;
              float: right;
              margin-top: 2px;
              right: 0;
              top: 0;
              .printIcon {

                background-color: @gray1;
                height: 34px;
                width: 34px;
              }
            }
            .printPage {
              width: 500px;
              position: absolute;
              top: 0;
              bottom: 0;
              left: 0;
              right: 0;
              margin: auto;
              height: 500px;
              background: @gray;
              display: none;
              .printWrapper {
                position: relative;
                //margin-top: 1px;
                //margin-bottom: 1px;
                //margin-left: 1px;
                //margin-right: 1px;

                //padding-bottom: @imgBoxDistance;
                //padding-top: @imgBoxDistance;
                //padding-left: @imgBoxDistance;
                //padding-right: @imgBoxDistance;

                float: left;
                border: @imgBoxDistance solid @imgBoxColor ;;
                //border: solid;
                //border:@lineBorder solid white;
                //border: @lineBorder solid white;

                //单个图像布局
                .singleViewport {
                  border: @imgBoxDistance solid @black ;
                  //touch-action: none;
                  //-webkit-user-select: none;
                  //-webkit-user-drag: none;
                  //-webkit-tap-highlight-color: rgba(0, 0, 0, 0);


                  background: @black;
                  text-align: center;
                  //font-size: 30px;

                  width: 100%;
                  height: 100%;
                  float: left;
                  .singleViewportInner {
                    background: @black;
                    float: left;
                  }
                  //丁字型合并
                  .singleMergeT1 {
                    position: absolute;
                    top: 0;
                    left: 0;

                  }
                  .singleMergeT2 {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                  }
                  .singleMergeT3 {
                    position: absolute;
                    top: 0;
                    right: 0;
                  }
                  .singleMergeT4 {
                    position: absolute;
                    bottom: 0;
                    right: 0;
                  }
                  .singleMergeT5 {

                  }
                  .singleMergeT6 {

                  }
                  .singleViewportInnerBox {
                    position: relative;
                    background: @black;
                    border: @lineBorderSelec solid mediumorchid !important;
                    float: left;
                  }
                  //格子编号
                  //.singleViewportNo{
                  //  position:relative;
                  //  font-size: 30px;
                  //  //line-height:100%;
                  //}
                }
                //多个图像布局
                .multiViewport {
                  border: @imgBoxDistance solid @black ;
                  //touch-action: none;
                  //-webkit-user-select: none;
                  //-webkit-user-drag: none;
                  //-webkit-tap-highlight-color: rgba(0, 0, 0, 0);

                  position: relative;
                  background: @black;
                  text-align: center;
                  float: left;
                  //width: 100%;
                  //height: 100%;
                  //float: left;
                  .multiViewportInner {
                    background: @black;
                    float: left !important;
                  }
                  //丁字型合并
                  .multiMergeT1 {
                    position: absolute;
                    top: 0;
                    left: 0;

                  }
                  .multiMergeT2 {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                  }
                  .multiMergeT3 {
                    position: absolute;
                    top: 0;
                    right: 0;
                  }
                  .multiMergeT4 {
                    position: absolute;
                    bottom: 0;
                    right: 0;
                  }
                  .multiMergeT5 {

                  }
                  .multiMergeT6 {

                  }
                  .multiViewportInnerBox {
                    background: @black;
                    border: @lineBorderSelec solid mediumorchid !important;
                    float: left !important;
                  }
                }

                .imgViewportSelected {
                  border: 1px solid @selecBorderColor !important;;
                }
                .imgBoxSelected {
                  border: @lineBorder solid @selecBorderColor !important;;
                }
                //图像信息
                .printOverlay {
                  position: absolute;
                  color: white;
                  font-size: 10px;

                }
              }
            }
          }

        }
        .rightBox {
          width: @width;
          background: @bule1;
          position: absolute;
          top: 30px;
          height: 100%;
          right: 0;
          padding: 5px 5px;
          .filmBox, .printBox, .iconBox {
            padding: 5px;
            border: 0.1em solid @write;
            border-radius: 5px;
            .filmBox-context {
              margin-top: 10px;
              .paperDirection, #paperSize {
                width: 113px;
              }
              .filmLayout {
                width: 100% !important;
              }
              .filmLayout, .merge, .breakup {
                display: inline-block;
                width: 60%;
                line-height: 25px;
                outline: none;
                color: @write;
                border: 1px solid #6C6F85;
                .diy-gradient(#B5B9C7, #6C6F85);
                float: left;
              }
              .breakup {
                width: 39% !important;
                float: right !important;
              }
              .printToolbar {
                width: 100%;
                text-align: center;
                font-size: 16px;
                //font: 华文细黑;
                .fa {
                  font-size: 12px;
                  padding: 5px 5.1px;
                  cursor: pointer;
                  border-radius: 2px;
                  .diy-gradient(#B5B9C7, #6C6F85);
                  &:hover {
                    background: @gray1;
                  }
                }
                input {
                  width: 30px;
                }
              }
            }
          }
          .printBox {
            background: @blue2;
            overflow: hidden;
            select {
              width: 100%;
            }
            .inputBox {
              .inputContent {
                float: left;
                input {
                  width: 116px;
                }
              }
              .inputBtnBox {
                float: left;
                width: 20px;
                height: 20px;
                position: relative;
                .inputBtn {
                  color: #000000;
                  position: absolute;
                  width: 20px;
                  height: 12px;
                  line-height: 7px;
                  cursor: pointer;

                }
                .btn1 {
                  top: 0;
                }
                .btn2 {
                  top: 12px;
                }
              }
            }

            .btn, .pCurBtn, .pAllBtn，.pLocal {
              display: inline-block;
              width: 100%;

              line-height: 25px;
              padding: 0;
              .diy-gradient(#B5B9C7, #6C6F85);
              border: 1px solid #6C6F85;
            }
            .pCurBtn {
              width: 49% !important;
              float: left;
            }
            .pAllBtn {
              width: 49% !important;
              float: right;
            }
            .pLocal {
              width: 49% !important;
              float: right;
            }

          }
          .radioBox {
            overflow: hidden;
            .radio {
              float: left;
            }
            .radioSpan {
              padding: 1px 5px;
              float: left;
              margin-top: 2px
            }
          }
          .iconBox {
            overflow: hidden;
            .inputContent {
              select {
                width: 60px;
              }
              input {
                width: 50px;
              }

            }
            .iconBtnBox {
              margin-top: 10px;
              li {
                width: 40px;
                height: 40px;
                margin-left: 1px;
                margin-right: 1px;
                margin-bottom: 2px;
                float: left;
              }
            }
            .directionLi {
              li {
                width: 40px;
                height: 40px;
                margin-left: 1px;
                margin-right: 1px;
                float: left;
                text-align: center;
                line-height: 41.5px;
                cursor: pointer;
                .diy-gradient(#B5B9C7, #6C6F85);
              }
            }
          }
          .btnBox {
            margin-top: 10px;
            text-align: center;
            button {
              margin-left: 10px;
              height: 25px;
              line-height: 25px;
              text-align: center;
              .diy-gradient(#B5B9C7, #6C6F85);
              margin-right: 10px;
              padding: 0 10px;
              border: none;
              outline: none;
              color: @write;
              &:hover {
                background: @gray1 !important;
              }
            }
          }
        }
      }

    }
  }
}

//合并弹出层
.merge {
  position: relative;
  .mergeUl {
    padding: 10px 10px 10px 12px;
    position: absolute;
    border-radius: 5px;
    top: 25px;
    z-index: 999;
    display: none;
    left: -25px;
    width: 200px;
    background: @black;
    li {
      width: 40px;
      height: 40px;
      text-align: center;
      cursor: pointer;
      float: left;
      margin: 2px;

      &:hover {
        background-color: #6493CF;
        color: white;
      }
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}

